import {PubgAPI} from 'pubg-api-redis';
import {PlayerStats} from 'pubgStats';
// also import PubgAPIErrors

class CommandAdapter
{
	api: PubgAPI;

	constructor(api: PubgAPI)
	{
		this.api = api;
	}

	handleCommand(userName: string, commandText: string): Promise<string>
	{
		// TODO ALL THIS
		const result = 'not supported ' + userName;
		switch (commandText) {
			case 'stats':
			{
				// console.log('stats');
				return this.api.profile.byNickname(userName).then((data: PlayerStats) => {
					console.log(data.Stats);
					console.log(data.Stats[0].Stats);
					return 'KDR: ' + data.Stats[0].Stats[0].displayValue;
				});
				// console.log('stats');
			}
			case 'kd':
			case 'kdr':
			{
				return this.api.profile.byNickname(userName).then((data) => {
					console.log(data.Stats);
					console.log(data.Stats[0].Stats);
					return data.Stats[0].Match + ': ' + data.Stats[0].Stats[0].label + ' : ' + data.Stats[0].Stats[0].displayValue + '(top ' + data.Stats[0].Stats[0].percentile +  ')';
				});
				// console.log('kd');
				// break;
			}
			case 'rating':
			{
				console.log('rating');
				break;
			}
			case 'wins':
			{
				console.log('wins');
				break;
			}
			case 'top10':
			{
				console.log('top10');
				break;
			}
			default:
				break;
		}
		return Promise.reject(result);
		// return result;
	}
}

export default CommandAdapter;